:root {
  --primary-color: 32, 101, 178;
  --secondary-color: 45, 93, 254;
  --tertiary-color: 203, 227, 255;
  --primary-complement: 254, 156, 45;
  --secondary-complement: 178, 109, 32;
  --text-color: 70, 70, 79;
  --lato: "Lato", sans-serif;
  --playfair: "Playfair Display", serif;
}

* {
  margin: 0;
  padding: 0;
}

html {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  scroll-behavior: smooth;
  font-size: 62.5%;
  width: 100vw;
  box-sizing: border-box;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-size: 16px;
  font-size: 1.6rem;
  line-height: 2;
  font-family: var(--lato);
  width: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/*Headings*/
h1,
h2,
h3,
h4,
h5 {
  font-family: var(--playfair);
  color: rgba(var(--primary-color));
}

h1,
.h1 {
  font-size: 4.2rem;
  font-weight: 700;
  line-height: 5.2rem;
}
@media (min-width: 768px) {
  h1,
  .h1 {
    font-size: 4.6rem;
    line-height: 5.6rem;
  }
}
@media (min-width: 1024px) {
  h1,
  .h1 {
    font-size: 5rem;
    line-height: 6rem;
  }
}

h2,
.h2 {
  font-size: 2.2rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  h2,
  .h2 {
    font-size: 2.8rem;
  }
}
@media (min-width: 1024px) {
  h2,
  .h2 {
    font-size: 3.2rem;
  }
}

h3,
.h3 {
  font-size: 1.8rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  h3,
  .h3 {
    font-size: 2.2rem;
  }
}
@media (min-width: 1024px) {
  h3,
  .h3 {
    font-size: 2.8rem;
  }
}

h4,
.h4 {
  font-size: 1.6rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  h4,
  .h4 {
    font-size: 1.8rem;
  }
}
@media (min-width: 1024px) {
  h4,
  .h4 {
    font-size: 2.2rem;
  }
}

/*Globales*/
a {
  font-family: var(--lato);
  color: rgb(var(--primary-complement));
  text-decoration: none;
  transition: color 0.2s ease-in-out;
}

a:hover,
a:active,
a:focus {
  color: rgb(var(--secondary-complement));
  /* text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.25); */
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li a {
  width: 100%;
  height: 100%;
  display: block;
}

p {
  font-family: var(--lato);
  color: rgb(var(--text-color));
  font-size: 1.6rem;
}

img {
  max-width: 100%;
  height: auto;
  opacity: 1;
  transition: opacity 0.3s;
}

img[data-src] {
  opacity: 0;
}

.txt-center {
  text-align: center;
}

.container {
  max-width: 1400px;
  width: 90%;
  margin: 0 auto;
}

.formatted-text {
  font-size: 1.6rem;
  font-family: var(--lato);
  color: rgba(var(--text-color), 1);
  line-height: 2.4rem;
}

.section-title {
  font-size: 2.6rem;
  font-family: var(--playfair);
  font-weight: bold;
  color: rgb(var(--primary-complement));
  letter-spacing: 2px;
  line-height: 3rem;
  text-transform: capitalize;
}
@media (min-width: 768px) {
  .section-title {
    font-size: 3rem;
    line-height: 3.4rem;
  }
}
@media (min-width: 1024px) {
  .section-title {
    font-size: 4rem;
    line-height: 4.4rem;
  }
}

.btn-primary {
  min-width: fit-content;
  padding: 5px 8px;
  background-color: rgba(var(--primary-complement), 1);
  display: inline-block;
  border-radius: 5px;
  font-family: var(--playfair);
  font-size: 2rem;
  font-weight: bold;
  color: #fff;
  letter-spacing: 2px;
  text-transform: capitalize;
  text-align: center;
  transition: 0.2s ease-in-out;
}
.btn-primary:hover {
  background-color: rgb(var(--secondary-complement), 1);
  color: #fff;
}

.site-header {
  width: 100%;
  background-color: rgba(var(--primary-color), 1);
  position: sticky;
  top: 0;
  z-index: 999;
}
.site-header .cont-nav {
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  padding: 15px 0;
}
.site-header .cont-nav #logo {
  display: inline-block;
  width: 75px;
  height: 75px;
}
.site-header .cont-nav #logo img {
  width: 100%;
  height: 100%;
}
.site-header .cont-nav #menu-mobile {
  width: 40px;
  height: 30px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
.site-header .cont-nav #menu-mobile.active .line:first-child {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  transform: rotate(45deg);
}
.site-header .cont-nav #menu-mobile.active .line:nth-child(2n) {
  opacity: 0;
}
.site-header .cont-nav #menu-mobile.active .line:last-child {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  transform: rotate(-45deg);
}
.site-header .cont-nav #menu-mobile .line {
  width: 100%;
  height: 2px;
  background-color: rgba(var(--tertiary-color), 1);
  transition: 0.2s ease;
}
.site-header .cont-nav .main-menu {
  width: 100%;
  max-height: 70vh;
  overflow-y: scroll;
  padding: 2rem 1.5rem;
  position: absolute;
  top: 100%;
  left: 0;
  display: none;
  background-color: rgba(var(--primary-color), 1);
  border-top: 1px solid rgba(var(--tertiary-color));
  transition: display 0.2s ease;
  box-shadow: 0px 12px 15px -6px rgba(0, 0, 0, 0.5);
}
.site-header .cont-nav .main-menu.active {
  display: inline-block;
}
.site-header .cont-nav .main-menu .menu {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.site-header .cont-nav .main-menu .menu .menu-item {
  width: 100%;
  display: flex;
}
.site-header .cont-nav .main-menu .menu .menu-item:hover, .site-header .cont-nav .main-menu .menu .menu-item.active, .site-header .cont-nav .main-menu .menu .menu-item.current-menu-ancestor, .site-header .cont-nav .main-menu .menu .menu-item.current-menu-item {
  display: block;
}
.site-header .cont-nav .main-menu .menu .menu-item:hover a, .site-header .cont-nav .main-menu .menu .menu-item:hover::after, .site-header .cont-nav .main-menu .menu .menu-item.active a, .site-header .cont-nav .main-menu .menu .menu-item.active::after, .site-header .cont-nav .main-menu .menu .menu-item.current-menu-ancestor a, .site-header .cont-nav .main-menu .menu .menu-item.current-menu-ancestor::after, .site-header .cont-nav .main-menu .menu .menu-item.current-menu-item a, .site-header .cont-nav .main-menu .menu .menu-item.current-menu-item::after {
  color: rgba(var(--primary-complement));
}
.site-header .cont-nav .main-menu .menu .menu-item-has-children {
  position: relative;
  display: flex;
  flex-direction: column;
}
.site-header .cont-nav .main-menu .menu .menu-item-has-children::after {
  content: "â–¾";
  padding: 0.5rem 0;
  position: absolute;
  top: 0.6rem;
  left: 90%;
  z-index: 2;
  font-size: 1.8rem;
  line-height: 1.8rem;
  transition: 0.2s ease-in-out;
  cursor: pointer;
}
.site-header .cont-nav .main-menu .menu .menu-item-has-children.active > .sub-menu {
  display: flex;
}
.site-header .cont-nav .main-menu .menu .menu-item-has-children.active:hover::after {
  transform: rotate(180deg);
}
.site-header .cont-nav .main-menu .menu .menu-item-has-children .sub-menu {
  padding-left: 1rem;
  display: none;
  flex-direction: column;
}
.site-header .cont-nav .main-menu .menu .menu-item a {
  width: fit-content;
  padding: 0.5rem 1rem;
  font-family: var(--playfair);
  font-size: 1.8rem;
  font-weight: 600;
  letter-spacing: 1.5px;
  text-transform: capitalize;
  color: #fff;
  transition: 0.2s ease-in-out;
}

#site-footer {
  background-color: rgba(var(--primary-color), 1);
}
#site-footer .footer-title {
  margin-bottom: 3rem;
  flex: 0 0 100%;
  font-family: var(--lato);
  font-size: 2.5rem;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  color: rgba(var(--tertiary-color));
}
#site-footer .footer-inner {
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5rem;
}
@media (min-width: 768px) {
  #site-footer .footer-inner {
    display: grid;
    grid-template-columns: 30% 25% 45%;
    column-gap: 0;
    row-gap: 5rem;
  }
}
@media (min-width: 1024px) {
  #site-footer .footer-inner {
    grid-template-columns: 33% 33% 34%;
  }
}
#site-footer .footer-inner .logo {
  display: none;
}
@media (min-width: 768px) {
  #site-footer .footer-inner .logo {
    grid-row: 1/3;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  #site-footer .footer-inner .logo img {
    width: 200px;
    height: 200px;
    margin-bottom: 2.5rem;
  }
  #site-footer .footer-inner .logo .footer-title {
    font-family: var(--playfair);
  }
}
#site-footer .footer-inner .locations {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  #site-footer .footer-inner .locations {
    grid-row: 1/2;
    grid-column: 3/4;
  }
}
#site-footer .footer-inner .locations .footer-menu-container,
#site-footer .footer-inner .locations .menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  #site-footer .footer-inner .locations .footer-menu-container,
  #site-footer .footer-inner .locations .menu {
    align-items: flex-start;
  }
}
#site-footer .footer-inner .locations .footer-menu-container {
  flex: 0 1 40%;
}
#site-footer .footer-inner .locations .footer-menu-container .footer-title {
  margin-bottom: 1.2rem;
  font-size: 2rem;
}
#site-footer .footer-inner .locations .footer-menu-container a {
  font-family: var(--lato);
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 768px) {
  #site-footer .footer-inner .locations .footer-menu-container a {
    text-align: left;
  }
}
#site-footer .footer-inner .locations .footer-menu-container .menu li {
  margin-bottom: 1rem;
}
#site-footer .footer-inner .locations .footer-menu-container .menu li a {
  margin: 0;
  color: #fff;
  transition: 0.2s ease-in-out;
}
#site-footer .footer-inner .locations .footer-menu-container .menu li a:hover {
  color: rgba(var(--primary-complement), 1);
}
#site-footer .footer-inner .locations .footer-menu-container .view-all {
  text-decoration: underline;
  color: rgba(var(--primary-complement), 1);
  transition: 0.2s ease-in-out;
}
#site-footer .footer-inner .locations .footer-menu-container .view-all:hover {
  color: rgba(var(--secondary-complement), 1);
}
@media (min-width: 768px) {
  #site-footer .footer-inner .social {
    grid-column: 3/4;
    grid-row: 2/3;
  }
}
#site-footer .footer-inner .social__icons {
  max-width: 60%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#site-footer .footer-inner .social__icons .icon {
  width: 35px;
  height: 35px;
}
@media (min-width: 768px) {
  #site-footer .footer-inner .social__icons .icon {
    width: 45px;
    height: 45px;
  }
}
#site-footer .footer-inner .social__icons .icon svg {
  width: 100%;
  height: 100%;
  fill: rgba(var(--tertiary-color), 1);
  transition: 0.2s ease-in-out;
}
#site-footer .footer-inner .social__icons .icon:hover svg {
  fill: rgba(var(--primary-complement), 1);
}
#site-footer .legal {
  width: 100%;
  padding: 1.5rem 0;
  background-color: rgba(var(--text-color));
}
#site-footer .legal p {
  font-family: var(--lato);
  font-size: 2rem;
  font-weight: bold;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
}